(function() {
  'use strict';

  angular
    .module('wmGitHubUib')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      controller: NavbarController,
      controllerAs: 'navbarCtrl',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($document, $window) {
      var sideNavIsOpen = false;

      var vm = this;
      vm.navbarCollapsed = true;
      vm.sideNavToggle = sideNavToggle;
      vm.navbarCollapse = navbarCollapse;

      function navbarCollapse() {
        if ($window.innerWidth < 768) {
          vm.navbarCollapsed = !vm.navbarCollapsed;
        }
      }

      function sideNavToggle() {
        sideNavIsOpen ? closeNav() : openNav();
        if (!vm.navbarCollapsed) {
          vm.navbarCollapsed = !vm.navbarCollapsed;
        }
        sideNavIsOpen = !sideNavIsOpen;
      }

      function openNav() {
        angular
          .element($document[0].querySelector('#mySidenav'))[0]
          .style.width = "250px";
      }

      function closeNav() {
        angular
          .element($document[0].querySelector('#mySidenav'))[0]
          .style.width = "0";
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('wmGitHubUib')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 100,
        deleteSpeed: 80,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

