(function() {
  'use strict';

  angular
    .module('wmGitHubUib')
    .controller('CvController', CvController);

  /** @ngInject */
  function CvController($window, $http, $log) {
    var vm = this;
    vm.justifyTabs = true;
    vm.skills = null;
    vm.colors = {
      'center': 'white',
      'highlight': 'orange',
      'remaining': 'lightGray'
    };

    activate();

    function activate() {
      if ($window.innerWidth < 768) {
        vm.justifyTabs = false;
      }
      $http.get('/assets/skills.json')
        .then(function(response) {
          vm.skills = response.data.data;
          angular.forEach(vm.skills, function(skill) {
            skill.rank = Math.random();
          });
        })
        .catch(function(error) {
          $log.error(error.data, error.status, error.statusText);
        });
    }

  }

})();

